	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Teenage Mum Home Office","date":"25 Mar 2005","slug":"/2005-03-25-teenage-mum-home-office/","tags":"Y2005"},"html":"<p><a href=\"http://www.boardsmag.com/screeningroom/animation/1146/\" title=\"'boards - Screening Room\">Sims-style \"Teenage Mum\" public service ad</a>. Doing home office with a child isn't much different. <a href=\"http://www.boingboing.net/2005/03/23/simsstyle_antiteenpr.html\">(Boing Boing)</a></p>"},"next":{"fields":{"title":"Respect!","date":"24 Mar 2005","slug":"/2005-03-24-respect/"}},"previous":{"fields":{"title":"Flower Pot","date":"26 Mar 2005","slug":"/2005-03-26-flower-pot/"}}},{"node":{"fields":{"title":"Respect!","date":"24 Mar 2005","slug":"/2005-03-24-respect/","tags":"Y2005"},"html":"<p><a href=\"http://jeremy.zawodny.com/blog/archives/004373.html\" title=\"Respect for Web Developers (by Jeremy Zawodny)\">Jeremy Zawodny:</a> </p>\n<blockquote><p>The problem for me, of course, is that the technology has advanced 4-5 years since I swore it off. That's a lot of time on the web.... \"I'm pretty smart, but this shit is hard.\"... I think a lot of people who've been doing \"web stuff\" long enough think that web developers are the same as the \"HTML monkeys\" we use to refer to back in the day. That couldn't be farther from the truth.</p></blockquote>\n<p>True. That's why I switched from web development to systems administration. Cross-system <a href=\"http://www.vim.org/\">vi</a> compatibility is a lot easier to deal with than cross-system browser (in)compatibility. (Not to mention that I have no design sense at all.) I'll let the experts like <a href=\"http://couchblog.de/webpropaganda/\">couch</a> and <a href=\"http://www.ichbinharmlos.de/blog/\">fab1An</a> make the sites look good. I'll stick to keeping them running.</p>"},"next":{"fields":{"title":"Carstensen in Exile","date":"24 Mar 2005","slug":"/2005-03-24-carstensen-in-exile/"}},"previous":{"fields":{"title":"Teenage Mum Home Office","date":"25 Mar 2005","slug":"/2005-03-25-teenage-mum-home-office/"}}},{"node":{"fields":{"title":"Carstensen in Exile","date":"24 Mar 2005","slug":"/2005-03-24-carstensen-in-exile/","tags":"Y2005"},"html":"<p>It's dangerous to watch news channels with the sound turned off. I glanced at <a href=\"http://www.n24.de/\">N24</a> this afternoon, and they were scrolling headlines from Kyrgyzstan while showing pictures of Peter Harry Carstensen. My first thought was that mobs had stormed the <em>Landtag</em> in Kiel and had forced the the CDU leader into exile. </p>\n<p>Given everything that's happened in Schleswig-Holstein the past few weeks, the thought isn't really that far fetched.</p>"},"next":{"fields":{"title":"Wonkette Disses Podcasters","date":"23 Mar 2005","slug":"/2005-03-23-wonkette-disses-podcasters/"}},"previous":{"fields":{"title":"Respect!","date":"24 Mar 2005","slug":"/2005-03-24-respect/"}}}],"pathPrefix":"","first":false,"last":false,"index":333,"pageCount":1101}}

